import mongoose from 'mongoose';
import Effect from './effect';
let Schema = mongoose.Schema;

let CommentSchema = new Schema({
  title: {
    type: String,
    required: true
  },
  text: String,
  effect: {
    type: Schema.Types.ObjectId,
    ref: 'effect',
    required: true
  }
});

module.exports = mongoose.model('Comment', CommentSchema);
